import React from 'react';
import { Button } from 'reactstrap';
import AbstractList from '../Core/AbstractList';

export default class GoodsListForOrder extends AbstractList {

	getEntityName() {
		return "createOrder";
	}

	getSearchPanel() {
		return [
			{ id: "LIKE:title", type: 'text', label: '' },
			{ id: "EQ:category.id", type: 'combo', label: '  ',
				values: this.state.options ? this.state.options.categories : [],
				convertToVal: it => it.id, convertToStr: it => it.title },
		]
	}

	getTableHead() {
		return [" ", ""];
	}

    createAble() {
        return false;
	}
	
	navigateToCreateOrder(orderId) {
		this.props.history.push('/create-order/' + orderId);
	}

	convertToTableRow(data) {
		return (
			<tr key={"item_" + data.id}>
				<td>{data.title}</td>
				<td>
                    <Button type="submit" size="sm" color="primary" onClick={this.navigateToCreateOrder.bind(this, data.id)}>
                        <i className="fa fa-plus" aria-hidden="true"></i>  
                    </Button>
				</td>
			</tr>
		);
	}

}


